<template>
  <v-sheet>
    <v-container>
      <v-switch
        @change="setTheme"
        v-model="theme"
        label=" "
      ></v-switch>

      <v-select
        v-model="groupItem"
        :items="groupList"
        label=""
        multiple
        chips
        hint="    "
        persistent-hint
        clearable
      ></v-select>

      <v-divider class="mt-5" />

      <v-subheader>  </v-subheader>
      <v-autocomplete
        hide-no-data
        hide-selected
        label=" "
        hint="    "
        persistent-hint
      />
      <v-autocomplete label=" " hint=" 12 " persistent-hint />
    </v-container>
    <settings-bottom-navigation />
  </v-sheet>
</template>

<script>
import SettingsBottomNavigation from "@/components/settings/SettingsBottomNavigation";
import { mapActions, mapMutations, mapState } from "vuex";

export default {
  name: "SettingsView",
  components: { SettingsBottomNavigation },
  data() {
    return {
      theme: false,
    };
  },
  computed: {
    ...mapState("settings", ["groupList", "selectedGroups"]),
    groupItem: {
      get() {
        return this.selectedGroups;
      },
      set(value) {
        this.updateSelectedGroups(value);
      },
    },
  },
  methods: {
    ...mapMutations(["setTitle"]),
    ...mapActions("settings", ["updateSelectedGroups"]),
    setTheme(value) {
      this.$vuetify.theme.dark = value;
      localStorage.setItem("darkTheme", this.$vuetify.theme.dark.toString());
    },
  },
  mounted() {
    this.setTitle("");
  },
  created() {
    this.theme = this.$vuetify.theme.dark;
  },
};
</script>
